@import "constant";

.player-wrapper {
  position: relative;
  // padding-top: 56.25%; /* 16:9 aspect ratio */
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  iframe,
  video {
    width: 853px;
    height: 480px;
    @include tablet {
      width: 640px;
      height: 360px;
    }
    @include phone {
      width: 560px;
      height: 315px;
    }
  }
}

// XL: 1280px x 720px
// L :  853px x 480px
// M :  640px x 360px
// S :  560px x 315px
