@import "constant";

footer {
  margin-top: 50px;
  padding-top: 50px;
  padding-bottom: 50px;
  border-top: 1px solid $color-gray;
  bottom: 0;
  @include phone {
    margin-top: 0;
    padding-top: 0;
    padding-bottom: 100px;
    // width: 90%;
    // margin-left: 5%;
  }
  p {
    margin-top: 20px;
    b {
      color: $color-orange;
      font-size: 1.2em;
      margin: 5px;
    }
  }
  .row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    width: 100%;
    height: 50px;
    margin-top: 20px;
    .col {
      height: 100%;
    }
  }
  a {
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    color: white;
    height: 100%;
    .icon {
      width: 30px;
      height: 30px;
      margin-right: 10px;
    }
    p {
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0;
      text-align: center;
      height: 100%;
      font-size: 20px;
      font-weight: bold;
      font-style: italic;
      &:hover {
        color: white;
        text-decoration: underline;
      }
      span {
        color: white;
        margin-right: 5px;
      }
    }
  }
}

.email {
  color: $color-green;
}
.facebook {
  color: $color-blue;
}
.instagram {
  color: $color-orange;
}
.tiktok {
  color: $color-red;
}
.threads {
  color: $color-yellow;
}
