@import "constant";

$width-new-movies-container: 835px;
$height-new-movies-container: 350px;
#home-page {
  .hot-movies-container {
    display: flex;
    // flex-direction: row;
    position: relative;
    width: $width-new-movies-container;
    height: $height-new-movies-container;
    margin-top: 25px;
    overflow: hidden;
    @include tablet {
      display: block;
      width: 420px;
    }
    @include phone {
      display: block;
      width: 100%;
      height: 300px;
      margin-top: 0;
      // left: -5vw;
    }

    .arrow-icon {
      position: absolute;
      width: 40px;
      height: 40px;
      top: 40%;
      // @include phone {
      //   top: auto;
      //   bottom: 0;
      // }
      opacity: 0.75;
      cursor: pointer;

      &:hover {
        opacity: 1;
      }
    }
    .arrow-right-icon {
      right: 0;
      @include tablet {
        right: 10px;
      }
      @include phone {
        right: 30px;
      }
    }
    .arrow-left-icon {
      left: 0;
      z-index: 3;
    }
  }
  .new-movies-container {
    margin-top: 50px;
    padding-top: 50px;
    border-top: 1px solid $color-gray;
    .nav-new-movies {
      display: flex;
      align-items: center;
      width: 100%;
      height: 50px;
      margin-bottom: 15px;
      @include phone {
        display: block;
        height: auto;
      }
      h2 {
        margin-right: 50px;
        text-align: center;
        @include phone {
          text-align: left;
          margin-bottom: 20px;
        }
      }
      .nav-items {
        display: flex;
        align-items: center;
        @include phone {
          width: 100%;
          display: flex;
          justify-content: space-around;
          align-items: center;
        }
        .nav-item {
          padding: 10px 20px;
          cursor: pointer;
        }
        .active {
          color: $color-red;
        }
      }
    }
    .list-movies-container {
      display: block;
      width: 100%;
      @include phone {
      }
      .movie-container {
        display: inline-block !important;
        margin-bottom: 20px;
        @include phone {
          height: auto;
        }
      }
      .button-container {
        display: flex;
        width: 100%;
        height: 50px;
        position: relative;
        // align-items: flex-end;
        .icon {
          margin-left: 10px;
        }
      }
      .button-24 {
        font-family: "Times New Roman", Times, serif;
        right: 0;
        position: absolute;
        align-items: center;
        display: flex;
      }
    }
  }

  .nav-category-container {
    display: block;
    margin-top: 40px;
    @include phone {
      margin-top: 20px;
    }
    a {
      display: inline-block;
      padding: 10px 20px;
      background-color: $color-gray;
      margin-right: 20px;
      margin-top: 20px;
      cursor: pointer;
      text-decoration: none;
      color: white;
      border-radius: 5px;
      @include phone {
        font-size: 0.8em;
        padding: 5px 10px;
        margin-right: 10px;
        margin-top: 10px;
      }
      &:hover {
        background-color: $color-green;
      }
    }
  }
}
