@import "constant";
@import url("https://fonts.googleapis.com/css2?family=Nabla&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Lobster&display=swap");

nav {
  display: block;
  position: relative;
  width: 100%;
  padding-bottom: 20px;

  .row {
    align-items: center;
    width: 100%;
  }

  .logo-container {
    display: flex;
    align-items: center;
    width: 20%;
    margin-right: 50px;
    text-decoration: none;
    @include phone {
      width: 80%;
    }

    img {
      width: 64px;
      height: 64px;
    }
    h1 {
      font-family: "Nabla", system-ui;
      font-optical-sizing: auto;
      font-weight: 400;
      font-style: normal;
      font-variation-settings: "EDPT" 100, "EHLT" 12;
      font-size: 2em;
      text-align: center;
      width: 100%;
      text-decoration: none;
      @include desktop {
        background-color: $color-red;
      }
      @include tablet {
        background-color: $color-yellow;
      }
      @include phone {
        // background-color: $color-green;
        text-align: left;
        margin-left: 20px;
        font-family: "Lobster", sans-serif;
        color: $color-yellow;
      }
    }
  }

  .search-container {
    display: flex;
    position: relative;
    @include desktop {
      width: 70%;
    }
    @include tablet {
      width: 60%;
      margin-left: 30px;
    }
    @include phone {
      margin-top: 20px;
      margin-bottom: 20px;
    }
    align-items: center;
    input {
      // width: clamp(300px, 400px, 500px);
      width: 90%;

      padding: 10px 15px;
      border-radius: 10px;
      border: 1px solid gray;
    }
    .search-icon {
      position: absolute;
      right: 15%;
      opacity: 50%;
      color: black;
    }
  }
  .icon-menu {
    display: none;
    @include phone {
      display: block;
      position: absolute;
      top: 40px;
      right: 40px;
      width: 40px;
      height: 40px;
      cursor: pointer;
      position: fixed;
    }
  }
  .icon-close-menu {
    z-index: 10;
    color: $color-red;
  }
  .hidden-menu {
    display: none;
  }
  .nav-menu {
    margin-top: 10px;
    @include phone {
      background-color: $color-dark-green;
      position: fixed;
      top: -50px;
      right: -50px;
      margin: 0;
      padding: 0;
      width: 70vw;
      height: 150vh;
      z-index: 9;
    }
    ul {
      display: flex;
      justify-content: space-between;
      align-items: center;
      list-style-type: none;
      height: 50px;
      @include phone {
        display: block;
        position: absolute;
        width: 100%;
        height: 100vh;
        top: 30vh;
      }

      li {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100%;
        width: 100%;
        border-bottom: 1px solid $color-blue;
        @include phone {
          height: 50px;
          border-bottom: transparent;
        }
        a {
          text-decoration: none;
          color: white;
          font-weight: bold;
          @include tablet {
            font-size: 0.8em;
          }
          &:hover {
            color: $color-green;
          }
        }
      }
      .active {
        border: 1px solid $color-blue;
        border-bottom: transparent;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        a {
          color: $color-red !important;
        }
        @include phone {
          height: 50px;
          border: transparent;
        }
      }
    }
  }
}
.version {
  position: absolute;
  top: -30px;
  right: 0;
  z-index: 99;
  color: white;
  opacity: 0.5;
}
