*{margin:0;padding:0;box-sizing:border-box}body{display:flex;justify-content:center;margin:0;padding:0;width:100vw;min-height:100vh;background-color:#6c757d;color:#fff;overflow-x:hidden}main{outline:1px solid red;background-color:#343a40;padding:40px;display:flex;flex-direction:column;min-height:100vh}@media(min-width: 1184px){main{width:calc(1200px - 40px)}}@media(min-width: 748px)and (max-width: 1183px){main{width:calc(768px);padding:2%}}@media(max-width: 751px){main{width:100vw;padding:2vw}}.container{width:100%}.row{display:flex}@media(max-width: 751px){.phone-row{display:block}}.disabled{opacity:.5;cursor:not-allowed;pointer-events:none}.error{color:red}.error::before{content:"⚠️"}a{text-decoration:none;color:#fff}