// constants.scss
$desktop-width: 1200px;
$tablet-width: 768px;
$phone-width: 320px;

// Media query mixins
@mixin desktop {
  @media (min-width: ($desktop-width - 16px)) {
    @content;
  }
}

@mixin tablet {
  @media (min-width: ($tablet-width - 20px)) and (max-width: ($desktop-width - 17px)) {
    @content;
  }
}

@mixin phone {
  @media (max-width: ($tablet-width - 17px)) {
    @content;
  }
}

$color-dark-green: #264653;
$color-green: #2a9d8f;
$color-yellow: #e9c46a;
$color-orange: #f4a261;
$color-red: #e76f51;
$color-blue: #8ecae6;
$color-gray: #6c757d;
$color-dark-gray: #343a40;
