@use "constant";
/* styles.css */
.loading-container-01 {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  .loading-spinner-01 {
    border: 10px solid constant.$color-dark-gray;
    border-radius: 50%;
    border-top: 10px solid constant.$color-green;
    width: 50px;
    height: 50px;
    -webkit-animation: spin 2s linear infinite; /* Safari */
    animation: spin 2s linear infinite;

    @keyframes spin {
      0% {
        transform: rotate(0deg);
      }
      100% {
        transform: rotate(360deg);
      }
    }
  }
}
