@import "constant";
@import url("https://fonts.googleapis.com/css2?family=Nabla&family=Tac+One&display=swap");

.movie-info-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 500px;
  width: 100%;
  height: 100%;
  .error {
    font-size: 2em;
    text-align: center;
  }
  .row {
    width: 98%;
  }
  .left-col {
    width: 70%;
    @include phone {
      width: 98%;
    }
    .movie-info {
      background-color: $color-dark-green;
      border-radius: 10px;
      position: relative;
      overflow: hidden;
    }
    .poster-container {
      width: 50%;
      position: relative;
      @include phone {
        width: 100%;
      }
      .poster {
        position: relative;
      }
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
      .button-container {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        bottom: 0;
        padding-top: 40px;
        padding-bottom: 20px;
        background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.3), black);
        @include tablet {
          padding-top: 20px;
          padding-bottom: 20px;
        }
        button {
          align-items: center;
        }
        .icon {
          margin-right: 5px;
        }
      }
    }
    .info {
      width: 40%;
      margin-left: 5%;
      padding-top: 20px;
      @include tablet {
        width: 46%;
        margin-left: 2%;
      }
      @include phone {
        width: 100%;
      }
      h1 {
        text-transform: uppercase;
        color: $color-orange;
        font-family: "Tac One", sans-serif;
      }
      h3 {
        opacity: 0.7;
      }
      .details {
        margin-top: 20px;
        p {
          margin-bottom: 10px;
          line-height: 1.6;
          @include tablet {
            line-height: 1;
          }
          span {
            opacity: 0.7;
            margin-right: 10px;
          }
        }
      }
    }
    .new-episodes-container {
      background-color: $color-dark-green;
      margin-top: 20px;
      padding: 20px;
      border-radius: 5px;
      display: flex;
      width: 100%;
      align-items: center;
      h3 {
        width: fit-content;
        margin-right: 20px;
      }
      .episodes-container {
        display: flex;
        flex-wrap: wrap;
        p {
          padding: 5px 10px;
          background-color: $color-gray;
          border-radius: 5px;
          cursor: pointer;
          margin-right: 10px;
          &:hover {
            background-color: $color-green;
          }
        }
      }
    }
    .extra-tags {
      margin-top: 20px;
      opacity: 0.5;
    }
    .movie-description-container {
      margin-top: 20px;
      padding: 20px;
      background-color: $color-dark-green;
      border-radius: 10px;
      h3 {
        margin-bottom: 10px;
      }
      p {
        line-height: 1.3;
        opacity: 0.8;
      }
    }
    .video-player-container {
      margin-top: 20px;
      padding: 20px;
      background-color: $color-dark-green;
      width: 100%;

      table {
        tr {
          td {
            padding: 20px 0px;
            @include phone {
              padding: 10px 0px;
            }
            &:nth-child(1) {
              width: fit-content;
              padding-right: 20px;
              font-weight: bold;
              opacity: 0.7;
            }
            &:nth-child(2) {
              display: flex;
              @include phone {
                flex-wrap: wrap;
              }
              p {
                padding: 10px;
                background-color: $color-gray;
                margin-right: 20px;
                border-radius: 7px;
                cursor: pointer;
                @include phone {
                  margin-bottom: 10px;
                  padding: 5px;
                }
                &:hover {
                  background-color: $color-green;
                }
              }
              .active {
                background-color: $color-orange;
              }
            }
          }
        }
      }
    }
  }
  .right-col {
    width: 30%;
    margin-left: 20px;
    @include tablet {
      margin-left: 10px;
    }
    @include phone {
      margin-left: 0;
      margin-top: 20px;
      padding-top: 20px;
      width: 100%;
      border-top: 1px solid $color-gray;
    }
    h2 {
      opacity: 0.7;
      margin-bottom: 10px;
    }
    .side-movies-container {
      display: block;
      position: relative;
      width: 100%;

      .side-movie-container {
        display: flex;
        width: 100%;
        height: 100px;
        margin-bottom: 10px;
        background-color: $color-gray;
        border-radius: 5px;
        overflow: hidden;
        @include phone {
          display: none;
        }
        .poster {
          display: block;
          width: 40% !important;
          height: 100px !important;
          position: relative;
          overflow: hidden;
          img {
            width: 100px !important;
            height: 100px !important;
            object-fit: cover;
          }
        }
        .side-movie-info {
          width: 60%;
          padding: 0 5%;
          h5 {
            color: $color-orange;
          }
          p {
            font-size: 0.7em;
            margin-top: 5px;
          }
          .title-en {
            opacity: 0.7;
          }
          .year {
            opacity: 0.7;
          }
          .status {
            color: $color-blue;
          }
        }
      }
      .movie-container {
        display: none;
        @include phone {
          display: inline-block;
          height: auto;
        }
      }
    }
  }
}
