@import "constant";

.ads-container {
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
  background-color: $color-gray;
  border-radius: 10px;
  overflow: hidden;
  @include phone {
    width: 100%;
    height: 100%;
  }
  img {
    object-fit: cover;
  }
  p {
    position: absolute;
    z-index: 9;
    bottom: 0;
    right: 0;
    opacity: 0.5;
  }
}

.ads-hot-movies {
  margin-top: 25px;
  width: 245px;
  height: 350px;
}
