@import "constant";

.movie-container {
  display: block;
  position: relative;
  width: 200px;
  height: 350px;
  margin-right: 10px;
  overflow: hidden;
  @include tablet {
    display: inline-block;
  }
  @include phone {
    display: inline-block;
    width: 150px;
    height: 350px;
    margin-right: 5px;
  }
  a {
    display: block;
    position: relative;
    width: 100%;
    height: 100%;
    text-decoration: none;
  }
  .movie-poster-container {
    display: flex;
    position: relative;
    width: 200px;
    height: 300px;
    overflow: hidden;
    cursor: pointer;
    background-color: $color-gray;
    border-radius: 10px;
    @include phone {
      width: 150px;
      height: 200px;
      border-radius: 5px;
    }
    img {
      width: 100%;
      height: 100%;
      transition: transform 0.3s ease-in-out;
      @include phone {
        object-fit: cover;
      }

      &:hover {
        transform: scale(1.2);
      }
    }
    p {
      position: absolute;
      background-color: $color-red;
      color: white;
      padding: 5px 10px;
      border-top-right-radius: 10px;
      border-bottom-left-radius: 10px;
      border: 1px solid $color-gray;
      @include phone {
        font-size: 0.8em;
      }
    }
    .status {
      left: 0;
      bottom: 0;
    }
    .year {
      right: 0;
    }
  }
  .movie-title-container {
    margin-top: 10px;
    width: 90%;
    overflow: hidden;

    h3 {
      color: $color-orange;
      text-decoration: none;
      white-space: nowrap;
      &:hover {
        color: $color-red;
      }
      @include phone {
        font-size: 0.8em;
      }
    }
    p {
      opacity: 0.7;
      color: white;
      white-space: nowrap;
      &:hover {
        color: $color-red;
      }
      @include phone {
        font-size: 0.8em;
      }
    }
  }
}
